/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  font-family: "Inter", sans-serif;
  margin: 0;
  padding: 0;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background: rgb(255, 255, 255);
  background: linear-gradient(0deg, rgb(255, 255, 255) 0%, rgb(255, 251, 251) 74%, rgb(255, 140, 135) 100%, rgb(255, 122, 116) 100%);
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** UI **/
.header {
  background-color: #a31a13;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  display: block;
}

.contenedor-header {
  border-width: 0px 0px 0.3rem 0px;
  border-color: #910000;
  border-style: solid;
  align-items: flex-start;
  flex-direction: column;
  justify-content: flex-start;
  padding: 1.8rem 2.4rem;
  flex-wrap: nowrap;
  height: min-content;
  position: relative;
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 2.4rem;
  width: 100%;
  height: auto;
  max-width: unset;
  height: min-content;
  padding: 0;
  position: relative;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: nowrap;
  }
}

.barra-arriba {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  width: 100%;
  height: min-content;
  padding: 0;
  position: relative;
}

.barra-img {
  width: auto;
  height: auto;
  position: relative;
  display: block;
  unicode-bidi: isolate;
}

.mobile-menu {
  width: 4rem;
  height: auto;
  position: relative;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mm1 {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  gap: 1rem;
  aspect-ratio: 1/1;
  cursor: pointer;
  padding: 0;
  position: relative;
}

.mm2 {
  width: 3.6rem;
  height: auto;
  position: relative;
}
.mm2 img {
  width: 3.6rem;
  height: auto;
}

.barra-abajo {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 2.4rem;
  display: none;
  width: 100%;
  height: min-content;
  padding: 0;
  position: relative;
  z-index: 8;
}
@media (min-width: 768px) {
  .barra-abajo {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: nowrap;
  }
}

.mostrar {
  display: flex;
}

.video {
  text-align: left;
  color: #ffffff;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.footer {
  background-color: rgba(156, 8, 0, 0.883);
  width: 100%;
  height: auto;
  flex: 0 0 auto;
}

.contenedor-footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 4.8rem;
  padding: 4rem 2.4rem 3.2rem;
  height: min-content;
  overflow: hidden;
}

.footer-arriba {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 4rem;
  width: 100%;
  height: min-content;
  overflow: visible;
}

.footer-img {
  width: 20rem;
  height: auto;
  flex: none;
  flex-shrink: 0;
}

/** copyright **/
.footer-abajo {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: nowrap;
  width: 100%;
  height: min-content;
  overflow: visible;
  z-index: 8;
}

.contenedor-copyright {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: nowrap;
  flex-shrink: 0;
  order: 1;
  width: auto;
  height: auto;
  user-select: none;
  white-space: pre-line;
  outline: none;
}

.copyright {
  color: #e39589;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.6rem;
  margin: 0;
  padding: 0;
  text-align: left;
}
.copyright a {
  color: #fffcfc;
  font-size: inherit;
  font-weight: 400;
  line-height: 1.6rem;
  margin: 0;
  padding: 0;
  text-decoration: underline !important;
  text-decoration-color: #e39589;
  text-decoration-style: solid;
  text-decoration-thickness: 0.1rem;
  text-underline-offset: 0.5rem;
  transition: color 0.3s cubic-bezier(0.12, 0.23, 0.5, 1);
  text-align: start;
  text-wrap-mode: nowrap;
  text-wrap-style: auto;
  white-space-collapse: preserve;
  cursor: pointer;
  box-sizing: border-box;
  letter-spacing: normal;
}
.copyright a:hover {
  color: #FF9080;
  text-decoration: none !important;
}
.copyright a::selection {
  background-color: #9F140F;
}

/** Parte header ⬆️ **/
.c-nav {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;
  gap: 0.4rem;
  width: 100%;
  height: min-content;
  position: relative;
}
@media (min-width: 768px) {
  .c-nav {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: nowrap;
  }
}

.c1-nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  gap: 1rem;
  width: min-content;
  height: min-content;
  position: relative;
}

.c2-nav {
  width: auto;
  height: 3.2rem;
  display: block;
  position: relative;
}
.c2-nav a {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  gap: 0.4rem;
  width: min-content;
  height: 100%;
  padding: 0.8rem 1.2rem;
  position: relative;
}

.c3-nav, .c2-nav-f, .t-nav-f {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: stretch;
  flex-wrap: nowrap;
  flex-shrink: 0;
  width: auto;
  height: auto;
  position: relative;
  white-space: pre;
  outline: none;
}
.c3-nav p, .c2-nav-f p, .t-nav-f p {
  color: #fffcfc;
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.6rem;
  margin: 0;
  padding: 0;
}
.c3-nav p:hover, .c2-nav-f p:hover, .t-nav-f p:hover {
  color: #FF9080;
}
.c3-nav p::selection, .c2-nav-f p::selection, .t-nav-f p::selection {
  background-color: #E39589;
}

/** Parte footer ⬇️ **/
.c-nav-f {
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  width: 100%;
}
@media (min-width: 768px) {
  .c-nav-f {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: nowrap;
    gap: 8rem;
    height: min-content;
    position: relative;
  }
}

.c1-nav-f {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 1.6rem;
  width: min-content;
  height: min-content;
  position: relative;
}

.t-nav-f p {
  color: #e39589;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.6rem;
  margin: 0;
  padding: 0;
}
.t-nav-f p::selection {
  background-color: #c43e32;
  color: #e39589;
}

.c2-nav-f p {
  color: #e39589;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.6rem;
  margin: 0;
  padding: 0;
}
.c2-nav-f p a {
  color: #e39589;
  font-size: inherit;
  font-weight: 400;
  line-height: 1.6rem;
  margin: 0;
  padding: 0;
  transition: color 0.3s cubic-bezier(0.12, 0.23, 0.5, 1);
  cursor: pointer;
  box-sizing: border-box;
  letter-spacing: normal;
  text-align: start;
  text-wrap-mode: nowrap;
  text-wrap-style: auto;
  white-space-collapse: preserve;
}
.c2-nav-f p a:hover {
  text-decoration: underline !important;
  text-decoration-color: #e39589;
  text-decoration-style: solid;
  text-decoration-thickness: 0.1rem;
  text-underline-offset: 0.5rem;
  color: #fffcfc;
}
.c2-nav-f p a::selection {
  background-color: #c43e32;
  color: #e39589;
}

/** Menu de hamburguesa **/
.mobile-menu {
  position: relative;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

/** logo **/
.logo {
  width: 20rem;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.8));
}

.logo-c {
  width: 10rem;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.8));
}

/**  **/
.mostrar {
  display: flex;
}

.video {
  position: relative;
  max-width: 100%;
  min-height: 100%;
  overflow: hidden;
  object-fit: cover;
}
.video .overlay {
  position: absolute;
  background: linear-gradient(270deg, rgba(52, 58, 64, 0.6), rgba(6.275862069, 7, 7.724137931, 0.9));
  inset: 0;
  display: flex;
  align-items: center;
}
.video video {
  min-width: 100%;
  min-height: 100vh;
  max-width: 100%;
  max-height: 100vh;
  object-fit: cover;
  display: block;
}
@media (min-width: 1024px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  justify-content: flex-start;
  flex-direction: column;
  padding: 0.8rem 2.4rem 0.8rem;
  z-index: 5;
}
.contenido-video h2 {
  display: flex;
  text-align: start;
  font-weight: 700;
  font-size: 7rem;
  margin: 0;
  display: block;
}
.contenido-video p {
  color: #ffffff;
  padding-top: 1rem;
  font-weight: 400;
  font-size: 1.5rem;
  margin: 0;
  display: block;
}

.services {
  margin-top: -135px;
  position: absolute;
  width: 100%;
}

.contenedor-carrusel {
  display: flex;
  justify-content: center;
  width: 100%;
  height: auto;
  align-items: center;
}

.card-wrapper {
  max-width: 1100px;
  margin: 0 60px 35px;
  padding: 20px 10px;
  overflow: hidden;
}

.card-list .card-item {
  list-style: none;
}

.card-list .card-item .card-link {
  user-select: none;
  display: block;
  background: #fff;
  padding: 18px;
  border-radius: 12px;
  text-decoration: none;
  border: 2px solid transparent;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  transition: 0.2s ease;
}

.card-list .card-item .card-link:active {
  cursor: grabbing;
}

.card-list .card-item .card-link:hover {
  border-color: #e39589;
}

.card-list .card-link .card-image {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  border-radius: 10px;
}

.card-list .card-link .badge {
  color: #c43e32;
  padding: 8px 16px;
  font-size: 0.95rem;
  font-weight: 700;
  margin: 16px 0 18px;
  background: #fffcfc;
  width: fit-content;
  border-radius: 50px;
}

.card-list .card-link .badge.asesoria {
  color: #c43e32;
  background: #ffded8;
}

.card-list .card-link .card-title {
  font-size: 1.19rem;
  color: #000;
  font-weight: 300;
}

.card-list .card-link .card-button {
  padding: 0;
  height: 35px;
  width: 35px;
  color: #a31a13;
  border-radius: 50%;
  margin: 30px 0 5px;
  background: none;
  cursor: pointer;
  border: 2px solid #a31a13;
  transform: rotate(-45deg);
  transition: 0.4s ease;
}

.card-list .card-link:hover .card-button {
  color: #fff;
  background: #a31a13;
}

.card-wrapper .swiper-pagination-bullet {
  height: 13px;
  width: 13px;
  opacity: 0.5;
  background: #a31a13;
}

.card-wrapper .swiper-pagination-bullet-active {
  opacity: 1;
}

.card-wrapper .swiper-slide-button {
  color: #a31a13;
  margin-top: -35px;
}

@media screen and (max-width: 768px) {
  .card-wrapper {
    margin: 0 10px 25px;
  }
  .card-wrapper .swiper-slide-button {
    display: none;
  }
}
.info-now {
  width: 100%;
  height: auto;
  flex: 1 1 auto;
}

.contenedor-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: nowrap;
  gap: 4.8rem;
  padding: 4rem 2.4rem 3.2rem;
  height: auto;
}
@media (min-width: 768px) {
  .contenedor-info {
    padding: 4rem 20rem 3.2rem;
  }
}

.fila {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .fila {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
  }
}

.columna-texto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 8rem;
}

.columna-completa h3 {
  font-weight: 700;
}
.columna-completa p {
  font-weight: 300;
}

.columna-acordeon {
  padding: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 8rem;
  background: #ffcac7;
  border-radius: 2.5rem;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1), 0px 8px 16px rgba(0, 0, 0, 0.1), 0px 16px 32px rgba(0, 0, 0, 0.05); /* sombra aún más difusa */
}

.acordeon {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2rem;
}

.acordeon-cabeza h3 {
  font-size: 3rem;
  font-weight: 700;
  color: #490906;
}
.acordeon-cabeza h3 span {
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.4));
}

.contenido-info p {
  font-weight: 300;
  color: #c31b12;
}

.facts-section {
  background-color: #f5f5f5;
  padding: 4rem 1.25rem;
}
@media (min-width: 768px) {
  .facts-section {
    padding: 4.5rem 2rem;
